(() => {
    const main = () => {
        // Iterate through all tables looking for containers.  This
        // works without assigning IDs to everything.
        const tbody_list = document.querySelectorAll("tbody")
        for (let i = 0; i < tbody_list.length; i++) {
            const tbody = tbody_list[i]
            const row_list = tbody.children
            for (let j = 1; j < row_list.length; j++) {
                const cur_row = row_list[j]
                if (cur_row.hasAttribute("compact-container")) {
                    const control_row = row_list[j-1]
                    cur_row.classList.add("table-container")
                    control_row.classList.add("table-container-ctrl")
                    control_row.addEventListener("click", (event) => {
                        if (control_row.hasAttribute("open")) {
                            cur_row.removeAttribute("open")
                            control_row.removeAttribute("open")
                        } else {
                            cur_row.setAttribute("open", "")
                            control_row.setAttribute("open", "")
                        }
                    })
                }
            }
        }
    }

    if (window.document$) {
        // Material specific hook
        window.document$.subscribe(main)
    } else {
        // Normal non-Material specific hook
        document.addEventListener("DOMContentLoaded", main)
    }

})()